
/* Modo claro */
.light-mode {
  background-color: white;
  color: black;
}

/* Modo oscuro */
.dark-mode {
  background-color: #212E36;
  color: white;
}

.contenedor {
  font-size: 1.5rem;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

select{
  font-size: 1.8rem;
  height: 3rem;
  width: 19rem;
  text-align: center;
}
input{
  font-size: 1.8rem;
  height: 3rem;
  width: 18.4rem;
  border-radius: 5px;
  text-align: center;
  border: 1px solid black;
  
}
h1{
  display: block;
  text-align: center;
  margin-bottom: 3rem;
}
button{
  margin: 2rem;
  font-size: 1.8rem;
  height: 4.5rem;
  width: 10rem;
  border-radius:10px;
  background-color: #3b82f6;
  color: white;
  border: 1px solid white;
  cursor: pointer;
  user-select: none;

}
button:active{
  background-color: #1d4ed8;
  color:white ;
  border: 1px solid #1d4ed8;
}
.selection{
  margin: 2rem;
  margin-left: 2rem;
  text-align: left;
  font-size: 1.8rem;
  height: 3.5rem;
  width: 19rem;
  border-radius:15px;
  color: white;
  cursor: pointer;
  position: relative;  
  z-index: 10; 
  
}
.selection2{
  margin: 1rem;
  margin-left: 1rem;
  text-align: left;
  font-size: 1.8rem;
  height: 3.5rem;
  width: 11rem;
  border-radius:15px;
  color: white;
  cursor: pointer;
  position: relative; 
  z-index: 10; 
  
}
.selection3{
  margin: 1rem;
  margin-left: 1rem;
  text-align: left;
  font-size: 1.8rem;
  height: 3.5rem;
  width: 19rem;
  border-radius:15px;
  color: white;
  cursor: pointer;
  position: relative; 
  z-index: 10;  
   }
.canvaContainer{
  
    display: flex;             /* Flexbox para el centrado */
    justify-content: center;   /* Centra el canvas horizontalmente */
    width: 100%;               /* Ocupa todo el ancho disponible */
    position: relative;  /* Asegura que el canvas esté en un contexto de posición */
    z-index: 1;  
  
}
.flechas{
  font-size: xx-large;
}
.menu-contenedor{
  
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 85vw;
  max-width: 750px;
  min-width: 300px;
  height: auto ;
  min-height: 300px;
  padding: 1rem;
  border: 1px solid gray;
  border-top: 0px;
  border-bottom-left-radius: 10px;  
  border-bottom-right-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}
.alarm-container {
  display: flex;
  flex-direction: column; /* Mantén la disposición en columna */
  align-items: center; /* Alineación centrada */
  width: 90%;
  margin: 0 auto; /* Centrar horizontalmente */
  padding: 16px;
  
  border-radius: 8px;
  
}

.alarmFlechas{
  font-size: xx-large;
  
}
.range-container {
  display: flex;
  justify-content: space-evenly;
  margin-top: 20px;
}
.range {
  display: flexbox; 
  align-items: center; 
  width: 3rem; 
  height: 2rem; 
  padding: 0px;
  margin-top: 0px;
  margin:1rem;
  background-color: white;
  color: black;
  border: none; 
  user-select: none;
  cursor: pointer; 
}

.bellowRange {
  display: flex;
  flex-direction: column;    /* Organiza los elementos verticalmente */
  justify-content: center;   /* Centra verticalmente */
  align-items: center;       /* Centra horizontalmente */
  width: 100%;               /* Ocupa todo el ancho disponible */
  gap: 20px;                 /* Espacio entre los elementos */
}

.select-container {
  display: flex;
  width: 100%;
  flex-direction: row;      
  align-items: center;     /* Coloca los selects uno encima del otro */
  justify-content: center;          /* Centrado vertical */
  gap: 10px;                        /* Espacio entre los selects */
}
.comparator{
  width: 4.5rem;
  border-radius: 8px;
}
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0; }
    .nav{
      display: flex;
      font-size: medium;
      text-decoration: none;
    }

    a{
      width: 100%;
      text-decoration: none;
      padding: 5px 20px;
      border: 1px solid gray;
      border-top-right-radius: 10px;
      border-top-left-radius: 10px;
    }
    a {
      color: inherit; /* Hereda el color según el contexto (modo claro/oscuro) */
      text-decoration: none; /* Elimina el subrayado si no lo deseas */
    }
    
    /* Elimina el color morado para los enlaces visitados */
    a:visited {
      color: inherit; /* Usa el color actual definido por el modo */
    }
    
    /* Estilo de los enlaces en modo claro */
    .light-mode a {
      color: black;
    }
    
    /* Estilo de los enlaces en modo oscuro */
    .dark-mode a {
      color: white;
    }
    .active{
      border-bottom: 0px;
      text-decoration: none;
    }
  
    @media (max-width: 768px) {
      .bellowRange {
        flex-direction: column; /* Cambia la disposición a columna en pantallas pequeñas */
      }
    
      .selection {
        min-width: 301px;
        width: 47%; /* Asegura que los selects ocupen el 100% del ancho disponible */
      }
      .selection2{
        width: 100%;
      }
    
      .canvaContainer {
        width: 100%; /* Asegura que el canvas ocupe el ancho disponible */
        padding: 1rem; /* Añadir un pequeño padding para mejor visualización */
        
      }
      
    }
    @media (max-width: 400px) {
      .selection3 {
        flex-direction: column;
        width: 65%; /* Cambia la disposición a columna en pantallas pequeñas */
      }
      
      .alarmInput{
        width: 62%;
      }
    
    }
    .table-container {
      width: 100%;
      overflow-x: auto; /* Desplazamiento horizontal en pantallas pequeñas */
      padding: 1rem;
      display: flex;
      flex-direction: column;
      gap: 10px;
    }
    table {
      border-collapse: collapse;
      width: 100%;
    }
    
    th, td {
      border: 1px solid #ddd; /* Cambia el color del borde para que sea más sutil */
      text-align: left;
      padding: 8px;
      font-size: 16px; /* Ajusta el tamaño de la fuente para mayor legibilidad */
    }
    
    th {
      background-color: rgb(97, 197, 255);
      color: black;
      position: sticky; /* Hace que la cabecera sea fija al hacer scroll */
      top: 0;
      z-index: 1;
    }
    
    td {
      background-color: rgb(194, 232, 254);
    }
    
    
.create {
  display: flex;
  flex-wrap: wrap; /* Para que los elementos se ajusten en varias filas si es necesario */
  align-items: center;
  justify-content: space-between;
  max-width: 80%;
  margin: 0 auto; /* Centrar el formulario horizontalmente */
  gap: 10px; /* Espacio entre los elementos */
}

.create input {
  flex: 1; /* Los inputs ocuparán el mismo espacio disponible */
  min-width: 10px; /* Ancho mínimo para los inputs */
  padding: 8px;
  height: 13px;
  font-size: 14px;
}

.create button {
  
  height: 35px;
  font-size: 14px;
  background-color: #007bff; /* Color de fondo del botón */
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  flex-shrink: 0; /* El botón no reducirá su tamaño */
}

.create button:hover {
  background-color: #0056b3; /* Cambia el color al pasar el ratón */
}
.botonModal {
  padding: 0; /* Eliminar padding adicional */
  margin: 0; /* Eliminar margen adicional */
  width: 25px; /* Ajusta el tamaño del botón según sea necesario */
  height: 25px; /* Ajusta la altura para que sea más pequeña */
  border: none; /* Elimina cualquier borde */
  cursor: pointer; /* Cambia el cursor a pointer */
}